import { useState, useCallback } from 'react';

interface InputContext {
  topics: string[];
  sentiment: number; // -1 to 1 scale
  intensity: number; // 0 to 1 scale
  isPotentialQuestion: boolean;
  inputType: 'text' | 'code' | 'url' | 'email' | 'unknown';
  recentInputs: string[];
}

export const useInputTracking = () => {
  const [inputHistory, setInputHistory] = useState<string[]>([]);
  
  // Simple function to identify input type based on content
  const identifyInputType = (input: string): InputContext['inputType'] => {
    if (input.startsWith('http') || input.includes('www.')) return 'url';
    if (input.includes('@') && input.includes('.')) return 'email';
    if (input.includes('{') || input.includes('function') || input.includes('const ') || input.includes('import ')) return 'code';
    return 'text';
  };
  
  // Extract potential topics from input
  const extractTopics = (input: string): string[] => {
    // This is a simplified version. In a real app, this would be much more sophisticated.
    // Could use NLP libraries or APIs for topic extraction
    const words = input.toLowerCase().split(/\s+/);
    const commonWords = new Set(['the', 'a', 'an', 'and', 'or', 'but', 'of', 'for', 'with', 'in', 'on', 'at']);
    
    return words
      .filter(word => word.length > 3 && !commonWords.has(word))
      .slice(0, 5); // Get up to 5 potential topics
  };
  
  // Simple sentiment analysis
  const analyzeSentiment = (input: string): number => {
    // This is a simplified version. In a real app, use NLP or ML for this.
    const positiveWords = ['good', 'great', 'excellent', 'awesome', 'love', 'like', 'best', 'happy'];
    const negativeWords = ['bad', 'awful', 'terrible', 'worst', 'hate', 'dislike', 'poor', 'sad'];
    
    const lowercaseInput = input.toLowerCase();
    let score = 0;
    
    positiveWords.forEach(word => {
      if (lowercaseInput.includes(word)) score += 0.2;
    });
    
    negativeWords.forEach(word => {
      if (lowercaseInput.includes(word)) score -= 0.2;
    });
    
    return Math.max(-1, Math.min(1, score)); // Clamp between -1 and 1
  };
  
  // Check if input might be a question
  const checkIfQuestion = (input: string): boolean => {
    return input.endsWith('?') || 
           input.toLowerCase().startsWith('what') || 
           input.toLowerCase().startsWith('how') ||
           input.toLowerCase().startsWith('why') ||
           input.toLowerCase().startsWith('when') ||
           input.toLowerCase().startsWith('where') ||
           input.toLowerCase().startsWith('which') ||
           input.toLowerCase().startsWith('who') ||
           input.toLowerCase().startsWith('can');
  };
  
  // Main function to analyze input and return context
  const trackInput = useCallback((input: string): InputContext => {
    // Add to history
    setInputHistory(prev => [...prev.slice(-9), input]);
    
    const inputType = identifyInputType(input);
    const topics = extractTopics(input);
    const sentiment = analyzeSentiment(input);
    const isPotentialQuestion = checkIfQuestion(input);
    
    // Calculate intensity based on punctuation, capitalization, etc.
    const exclamationCount = (input.match(/!/g) || []).length;
    const capitalRatio = input.split('').filter(char => char >= 'A' && char <= 'Z').length / input.length;
    const intensity = Math.min(1, (exclamationCount * 0.2) + (capitalRatio * 0.5) + 0.3); // Base intensity of 0.3
    
    return {
      topics,
      sentiment,
      intensity,
      isPotentialQuestion,
      inputType,
      recentInputs: [...inputHistory, input]
    };
  }, [inputHistory]);
  
  return { trackInput };
}; 