// Function to generate dynamic suggestions based on input and context
export const mockAIResponse = (
  input: string, 
  context: InputContext
): Suggestion[] => {
  // Set base confidence levels for suggestions based on input length and context
  const baseConfidence = Math.min(0.9, 0.5 + (input.length / 100));
  
  // Select appropriate suggestion set based on input type
  let baseSuggestions: Suggestion[] = [];
  
  if (context.isPotentialQuestion) {
    baseSuggestions = questionSuggestions;
  } else if (context.inputType === 'code') {
    baseSuggestions = codeSuggestions;
  } else if (context.inputType === 'url') {
    baseSuggestions = urlSuggestions;
  } else {
    baseSuggestions = textSuggestions;
  }
  
  // Randomly select 2-4 suggestions
  const count = Math.floor(Math.random() * 3) + 2; // 2 to 4 suggestions
  const shuffled = [...baseSuggestions].sort(() => 0.5 - Math.random());
  const selected = shuffled.slice(0, count);
  
  // Adjust confidence based on input context
  return selected.map(suggestion => {
    // Adjust confidence based on context
    const confidenceVariation = (Math.random() * 0.2) - 0.1; // -0.1 to +0.1
    const adjustedConfidence = Math.min(
      0.95, 
      Math.max(0.4, baseConfidence + confidenceVariation)
    );
    
    // Dynamic content modification based on input - in a real app, this would use AI
    let modifiedContent = suggestion.content;
    if (context.topics.length > 0) {
      const topic = context.topics[0];
      modifiedContent = modifiedContent.replace(
        /your (current topic|code|question)/g, 
        `your ${topic}`
      );
    }
    
    return {
      ...suggestion,
      confidence: adjustedConfidence,
      content: modifiedContent,
      id: `${suggestion.id}-${Date.now()}`
    };
  });
}; 