import React, { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import Canvas from './components/Canvas';
import SuggestionCard from './components/SuggestionCard';
import { useAIService } from './hooks/useAIService';
import { useInputTracking } from './hooks/useInputTracking';
import { theme } from './styles/theme';
import { Suggestion } from './types';

const AppContainer = styled.div`
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: ${({ theme }) => theme.colors.background};
  overflow: hidden;
  position: relative;
`;

const WelcomeMessage = styled(motion.div)`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  z-index: 5;
  
  h1 {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
    background: ${({ theme }) => theme.colors.gradients.primary};
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  p {
    font-size: 1.2rem;
    color: ${({ theme }) => theme.colors.lightText};
  }
`;

function App() {
  const [userInput, setUserInput] = useState('');
  const [showWelcome, setShowWelcome] = useState(true);
  const [suggestions, setSuggestions] = useState<Suggestion[]>([]);
  const [activeContext, setActiveContext] = useState<Suggestion | null>(null);
  const canvasRef = useRef(null);
  
  const { trackInput } = useInputTracking();
  const { getSuggestions } = useAIService();
  
  // Hide welcome message once user starts interacting
  useEffect(() => {
    if (userInput.length > 0 && showWelcome) {
      setShowWelcome(false);
    }
  }, [userInput, showWelcome]);
  
  // Track user input and get AI suggestions
  useEffect(() => {
    if (userInput.length > 0) {
      const debouncedGetSuggestions = setTimeout(async () => {
        const inputContext = trackInput(userInput);
        const newSuggestions = await getSuggestions(userInput, inputContext);
        setSuggestions(newSuggestions);
      }, 300);
      
      return () => clearTimeout(debouncedGetSuggestions);
    } else {
      setSuggestions([]);
    }
  }, [userInput, trackInput, getSuggestions]);
  
  // Handle card selection
  const handleCardClick = (suggestion: Suggestion) => {
    setActiveContext(suggestion);
    // When a card is clicked, it takes over the screen with more details
  };
  
  return (
    <AppContainer>
      <AnimatePresence>
        {showWelcome && (
          <WelcomeMessage
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.5 }}
          >
            <h1>AI Evolving Canvas</h1>
            <p>Start typing anywhere to begin...</p>
          </WelcomeMessage>
        )}
      </AnimatePresence>
      
      <Canvas 
        ref={canvasRef}
        userInput={userInput}
        setUserInput={setUserInput}
        activeContext={activeContext}
      />
      
      <AnimatePresence>
        {suggestions.map((suggestion, index) => (
          <SuggestionCard
            key={index}
            suggestion={suggestion}
            index={index}
            total={suggestions.length}
            onClick={() => handleCardClick(suggestion)}
          />
        ))}
      </AnimatePresence>
    </AppContainer>
  );
}

export default App; 